import {decrementCount} from '../ActionCreators';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import elementResizeDetectorMaker from 'element-resize-detector';
import 'metoclient';

class DecrementButton extends React.Component {

    componentDidMount() {

        // API-KEY is required as part of the base URL that is used for the layers
        // when FMI services are used.
        var API_KEY = 'YOUR-API-KEY';
        // Initialize options objects for animation.
        var resolutionTime = 5 * 60 * 60 * 1000;
        var currentDate = new Date();
        var currentTime = fi.fmi.metoclient.ui.animator.Utils.floorTime(currentDate.getTime(), 60 * 60 * 1000);
        var beginDate = new Date(currentTime);
        var endDate = new Date(currentTime);
        // Make sure begin time starts from the exact hour.
        // Then, timesteps will be on the proper positions when layers are requested.
        // If requested times do not match exactly FMI observation times, layers may
        // not contain any visible content.
        beginDate = fi.fmi.metoclient.ui.animator.Utils.floorDate(beginDate, 60 * 60 * 1000);
        var beginTime = beginDate.getTime();
        var endTime = endDate.getTime();
        var baseUrl = 'http://wms.fmi.fi/fmi-apikey/' + API_KEY + '/geoserver/';
        var wmsBaseUrl = baseUrl + 'wms';
        var wmtsBaseUrl = baseUrl + 'gwc/service/wmts';
        var resolutions = [2048, 1024, 512, 256, 128, 64];
        var matrixIds = [
            'ETRS-TM35FIN:2',
            'ETRS-TM35FIN:3',
            'ETRS-TM35FIN:4',
            'ETRS-TM35FIN:5',
            'ETRS-TM35FIN:6',
            'ETRS-TM35FIN:7'
        ];
        var origin = [-4537345.568, 8254755.58];
        var origins256 = [[-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352]];
        var origins512 = [[-118331.36640836, 8432773.1670142], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352]];
        var origins1024 = [[-118331.36640836, 8432773.1670142], [-118331.36640836, 8432773.1670142], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352], [-118331.36640836, 7907751.53726352]];
        var extent = [-118331.366408356, 6335621.16701424, 875567.731906565, 7907751.53726352];

        var metoclientConfig = {
            project: 'mymap',
            // Map view configurations
            map: {
                model: {
                    // Layer configuration
                    layers: [
                        // ---------------------------------------------------------------
                        {
                            "className": "WMTS",
                            "title": "Taustakartta",
                            "type": "map",
                            "visible": true,
                            "opacity": 1.0,
                            "sourceOptions": {
                                "matrixSet": "ETRS-TM35FIN",
                                "layer": "KAP:Europe_basic_EurefFIN",
                                "format": "image/png"
                            },
                            "tileCapabilities": "http://wms.fmi.fi/fmi-apikey/"+API_KEY+"/geoserver/gwc/service/wmts?request=GetCapabilities",
                            "animation": {
                                "hasLegend": false
                            }
                        },
                        {
                            "className": "WMTS",
                            "title": "Tutkahavainto",
                            "type": "obs",
                            "visible": true,
                            "opacity": 1.0,
                            "sourceOptions": {
                                "matrixSet": "ETRS-TM35FIN-FINLAND",
                                "layer": "Radar:suomi_rr_eureffin",
                                "format": "image/png"
                            },
                            "tileCapabilities": "http://wms.fmi.fi/fmi-apikey/"+API_KEY+"/geoserver/gwc/service/wmts?request=GetCapabilities",
                            "timeCapabilities": "http://wms.fmi.fi/fmi-apikey/"+API_KEY+"/geoserver/wms?request=GetCapabilities&service=WMS",
                            "animation": {
                                "beginTime": 1505371354395,
                                "hasLegend": "http://data.fmi.fi/fmi-apikey/"+API_KEY+"/dali?customer=legend&product=rr&width=100&height=250&type=png"
                            }
                        },
                        {
                            "className": "WMTS",
                            "title": "airports tma cta",
                            "type": "overlay",
                            "visible": true,
                            "sourceOptions": {
                                "matrixSet": "ETRS-TM35FIN",
                                "layer": "KAP:airports_tma_cta",
                                "format": "image/png"
                            },
                            "tileCapabilities": "http://wms.fmi.fi/fmi-apikey/"+API_KEY+"/geoserver/gwc/service/wmts?request=GetCapabilities",
                            "animation": {
                                "hasLegend": false
                            }
                        }
                    ]
                },
                view: {
                    container: 'fmi-animator',
                    projection: 'EPSG:3857',
                    extent: [-500000, 5000000, 5000000, 20000000],
                    resolutions: resolutions,
                    defaultCenterLocation: [2750000, 9000000],
                    defaultCenterProjection: 'EPSG:3857',
                    defaultZoomLevel: 0,
                    showLegend: true,
                    legendTitle: 'Legend',
                    noLegendText: 'None',
                    showLayerSwitcher: true,
                    showLoadProgress: true,
                    ignoreObsOffset: 5 * 60 * 1000,
                    maxAsyncLoadCount: 5,
                    // Disable panning and zooming
                    staticControls: false
                }
            },
            // Time configuration
            time: {
                model: {
                    autoStart: false,
                    waitUntilLoaded: false,
                    autoReplay: true,
                    refreshInterval: 60 * 60 * 1000,
                    frameRate: 500,
                    resolutionTime: resolutionTime,
                    defaultAnimationTime: (new Date()).getTime(),
                    beginTime: beginTime,
                    endTime: endTime,
                    endTimeDelay: 1000
                },
                view: {
                    showTimeSlider: false,
                    timeZone: 'Europe/Helsinki',
                    imageWidth: 55,
                    imageHeight: 55,
                    imageBackgroundColor: '#585858',
                    sliderOffset: 55,
                    sliderHeight: 55,
                    statusHeight: 12,
                    tickTextColor: '#000000',
                    pastColor: '#B2D8EA',
                    futureColor: '#D7B13E',
                    tickColor: '#FFFFFF',
                    notLoadedColor: '#585858',
                    loadingColor: '#B2D8EA',
                    loadedColor: '#94BF77',
                    loadingErrorColor: '#9A2500',
                    tickHeight: 24,
                    tickTextYOffset: 18,
                    tickTextSize: 12,
                    pointerHeight: 30,
                    pointerTextOffset: 15,
                    pointerColor: '#585858',
                    pointerTextColor: '#D7B13E',
                    pointerTextSize: 12
                }
            },
            localization: {
                locale: 'fi',
                fi: {
                    overlays: 'Sääaineistot',
                    baseLayers: 'Taustakartat',
                    features: 'Kohteet',
                    legend: 'Selite',
                    noLegend: 'Ei selitettä',
                    zoomInTooltip: 'Lähennä',
                    zoomOutTooltip: 'Loitonna',
                    layersTooltip: 'Karttatasot',
                    browserNotSupported: 'Tämä selain ei ole tuettu.'
                },
                en: {
                    overlays: 'Overlays',
                    baseLayers: 'Base layers',
                    features: 'Features',
                    legend: 'Legend',
                    noLegend: 'None',
                    zoomInTooltip: 'Zoom in',
                    zoomOutTooltip: 'Zoom out',
                    layersTooltip: 'Layers',
                    browserNotSupported: 'This browser is not supported.'
                }
            }
        };

        var config = jQuery.extend(true, {}, metoclientConfig);
        config.project = 'project-' + this.props.container;
        config.map.view.container = this.props.container;

        console.log(this.props.container);


        var metoclient = new fi.fmi.metoclient.ui.animator.MapLibrary(config);
        metoclient.createAnimation();

        var erd = elementResizeDetectorMaker();

        erd.listenTo(document.getElementById(this.props.container), function (element) {
            var width = element.offsetWidth;
            var height = element.offsetHeight;
            console.log("Size: " + width + "x" + height);
            metoclient.refreshMap();
        });
    }

    render() {
        return (
            <div>
                <button onClick={() => this.props.decrementCount()}>Decrement Count</button>
                <div id={this.props.container} className="map-container"></div>
            </div>
        );
    }
}

DecrementButton.PropTypes = {
    decrementCount: PropTypes.func.isRequired
};

function mapDispatchToProps(dispatch) {
    return {
        decrementCount: () => dispatch(decrementCount())
    };
}

export const DecrementButtonContainer = connect(
    null,
    mapDispatchToProps
)(DecrementButton);



